import { useState } from "react";
import useSWR from "swr";
import CryptoCard from "../components/CryptoCard";
import Filter from "../components/Filter";
import Header from "../components/Header";
import Footer from "../components/Footer";

const fetcher = url => fetch(url).then(res => res.json());

export default function Home() {
  const [timeframe, setTimeframe] = useState("24h");
  const [limit, setLimit] = useState(100);

  const { data: cryptos, error } = useSWR(
    `/api/topAlpha?timeframe=${timeframe}&limit=${limit}`,
    fetcher,
    { refreshInterval: 600000 }
  );

  if (error) return <div className="text-center mt-20">Error loading data</div>;
  if (!cryptos) return <div className="text-center mt-20">Loading...</div>;

  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="container mx-auto p-4 flex-1">
        <h1 className="text-3xl font-bold mb-4 text-center">BitTrend - Top 5% Alpha Cryptos</h1>
        <Filter timeframe={timeframe} setTimeframe={setTimeframe} limit={limit} setLimit={setLimit} />
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 mt-4">
          {cryptos.map(crypto => (
            <CryptoCard key={crypto.id} crypto={crypto} />
          ))}
        </div>
      </main>
      <Footer />
    </div>
  );
}