import { Sparklines, SparklinesLine } from "react-sparklines";

export default function CryptoCard({ crypto }) {
  const priceChangeClass =
    crypto.price_change_percentage_24h >= 0 ? "text-green-500" : "text-red-500";

  return (
    <div className="p-4 border rounded-lg shadow hover:shadow-lg transition bg-white dark:bg-gray-800">
      <div className="flex items-center justify-between mb-2">
        <h2 className="font-bold">{crypto.name}</h2>
        <img src={crypto.image} alt={crypto.name} className="w-8 h-8" />
      </div>
      <p className="text-gray-700 dark:text-gray-300">Symbol: {crypto.symbol.toUpperCase()}</p>
      <p className="text-gray-700 dark:text-gray-300">Price: ${crypto.current_price.toLocaleString()}</p>
      <p className={`${priceChangeClass} font-semibold`}>
        24h: {crypto.price_change_percentage_24h.toFixed(2)}%
      </p>

      {crypto.sparkline_in_7d?.price && (
        <div className="mt-2">
          <Sparklines data={crypto.sparkline_in_7d.price} svgWidth={100} svgHeight={30}>
            <SparklinesLine
              color={crypto.price_change_percentage_7d_in_currency >= 0 ? "green" : "red"}
              style={{ strokeWidth: 2, fill: "none" }}
            />
          </Sparklines>
        </div>
      )}
    </div>
  );
}